import axios from 'axios'

/**
 * Service vuex
 * modulenamespace
 * axiosactionspromise
 * actionsmutationstate
 * */

export default class Service {
    constructor(params) {
        let { namespaced = true, prefix, state = {}, mutations ={}, actions = {}, getters = {} } = params;
        this.namespaced = namespaced;
        this.prefix = prefix;
        this.state = state;
        this.mutations = mutations;
        this.actions = actions;
        this.getters = getters;
    }

    fetch(api, method, data) {
        let mtd = method.toLowerCase();
        let url = this.prefix + api;
        return mtd === 'get' ? axios.get(url, { params: data }) : axios.post(url, data);
    }

}