<template>
    <div :class="$style['staff-comment-container']">
        <div :class="$style.itemBox">
            <span></span>
            <span><input name="businessExperience" type="radio" v-model="businessExperience" value="0" />  &emsp;</span>
            <span><input name="businessExperience" type="radio" v-model="businessExperience" value="1" /> </span>
        </div>
        <div :class="$style.itemBox">
            <span></span>
            <span><input name="leaderExperience" type="radio" v-model="leaderExperience" value="0" />  &emsp;</span>
            <span><input name="leaderExperience" type="radio" v-model="leaderExperience" value="1" /> </span>
        </div>
        <div :class="$style.itemBox">
            <span></span>
            <span><input name="efficiency" type="radio" v-model="efficiency" value="0" />  &emsp;</span>
            <span><input name="efficiency" type="radio" v-model="efficiency" value="1" />  &emsp;</span>
            <span><input name="efficiency" type="radio" v-model="efficiency" value="2" />  &emsp;</span>
        </div>

        <div :class="$style.btnBox">
            <button @click="submit()"></button>
            <button @click="cancel()"></button>

        </div>

    </div>
</template>

<script>
	export default {
		name: "StaffCommentModal",
		data() {
			return {
				businessExperience: 0,
				leaderExperience: 0,
				efficiency: 0
			}
		},
		methods: {
			cancel() {
				this.$close();
			},
			submit() {
				let comments = [];
				comments.push(this.businessExperience ? "" : "");
				comments.push(this.leaderExperience ?  "" : "");
				comments.push(this.leaderExperience === 0 ? "" :
					this.leaderExperience === 1 ? "" : "");
				let staffComment = comments.join(",");
				this.$emit('submitComment', staffComment);
			}
		}
	}
</script>

<style lang="scss" module>
    .staff-comment-container {
        width: 60%;
        margin: 0 auto;
        background: #fff;
        border-radius: 10px;
        padding: 10px 20px;
        .itemBox {
            @include flex-row-between;
            width: 50%;
            margin: 30px 0;
        }
    }
</style>